







































































































































































































































































































































































































































































































































































.json-widget {
  .json-delim {
    font-weight: bold;
    color: midnightblue;
  }
  .json-collapse {
    line-height: 12px;
    padding: 0 4px;
    font-size: 12px;
  }
  .uk-button {
    &.json-type-chooser, &.json-add {
      padding: 2px 6px 3px;
      line-height: 13px;
      font-size: 13px;
      font-weight: bold;
      border-radius: 3px;
    }
    &.json-type-chooser {
      margin-right: 2px;
      background: transparent;
    }
  }
  > .uk-dropdown {
    padding: 0;
    > * {
      display: block;
      width: 100%;
      padding: 2px 10px;
      line-height: unset;
    }
  }
}
